/* About.css */

.about-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
    max-width: 1200px;
    margin: auto;
    gap: 20px;
}

.about-container1, .about-container2 {
    flex: 1;
    padding: 20px;
}

.about-container1 h4 {
    font-size: 2rem;
    color: #2c3e50; 
    font-family: 'sour gummy', cursive;
    margin-bottom: 20px;
}

.about-container1 p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #555; 
    text-align: justify;
}

.about-container2 img {
    width: 70%;
    max-width: 500px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
    .about-container {
        flex-direction: column;
        text-align: center;
    }
    
    .about-container1 h4 {
        font-size: 1.8rem;
    }
    
    .about-container2 img {
        max-width: 300px;
        margin: 20px auto;
    }
}

.reviews {
    padding: 40px;
    background-color: #f8f8f8;
    text-align: center;
    margin-bottom: 60px;
  }
  
  .review-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .review {
    background: white;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 250px;
    width: 100%; 
    flex: 1; 
    min-width: 220px; 
    transition: transform 0.2s; 
  }
  
  .review:hover {
    transform: scale(1.05); 
  }
  
  .profile-pic {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  
  .review h2 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.5rem;
  }
  
  .review h3 {
    color: #555;
    margin: 10px 0;
  }
  
  .stars {
    color: #ffb400;
    font-size: 1.2rem;
  }
  
  @media (max-width: 768px) {
    .review-container {
      flex-direction: column; 
      align-items: center; 
    }
  
    .review {
      max-width: 90%; 
      margin-bottom: 20px; 
    }
  }
  